@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.custom-button{font-family:"Roboto", Arial, Helvetica, sans-serif;padding:10px 20px;font-size:14px;text-transform:uppercase;color:#fff;border:none;cursor:pointer}.custom-button:hover:disabled{background-color:transparent}.custom-button:disabled{background:#fff;border:1px solid #e8503e;color:#2e3442}.custom-button:not(:last-child){margin:20px 10px 20px 0}.button-primary{background-color:#e8503e}.button-primary:hover{background-color:#2e3442}.button-dark{background-color:#2e3442}.button-dark:hover{background-color:#e8503e}

.gallery__item{position:relative;height:450px;width:50%;overflow:hidden}.gallery__item:hover .gallery__image{transform:scale(1.2)}.gallery__item--remove{position:absolute;right:0;top:0}.gallery__image{width:100%;height:100%;object-fit:cover;transition:transform 4s linear}@media (max-width: 768px){.gallery__item{width:100%}}

.gallery{display:flex;flex-wrap:wrap}

.pagination{margin-right:20px}.pagination__buttons{width:100%;display:flex;justify-content:center;align-items:center}.pagination__current-page{letter-spacing:1.4px;margin:0 10px}@media (max-width: 500px){.pagination{width:100%;margin-right:0}.pagination__buttons{flex-direction:column;margin-bottom:20px}.pagination__current-page{order:-1}.pagination .custom-button{margin-right:0}}

.filters{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;padding:20px 0}.limit-text{margin-right:15px}.limit{padding:10px 20px;max-height:20px}@media (max-width: 500px){.filters .custom-button{width:100%}}

.preloader{display:flex;justify-content:center;align-items:center;margin:40px 0}.preloader__inner{display:inline-block;width:60px;height:60px}.preloader__inner:after{content:" ";display:block;width:40px;height:40px;margin:8px;border-radius:50%;border:6px solid #fff;border-color:#e8503e transparent #2e3442 transparent;animation:preloading 1.2s linear infinite}@keyframes preloading{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.photo-page__image{max-width:100%}.photo-page__content{display:flex;justify-content:center;align-items:center;margin:20px 0}.photo-page__size{display:flex;justify-content:center;align-items:center}.photo-page__size-item,.photo-page__author{margin:10px}@media (max-width: 600px){.photo-page__content{flex-direction:column}}

.header{padding:20px 0;background-color:#2e3442;margin-bottom:20px}.header__link{position:relative;color:#fff;text-transform:capitalize}.header__link:hover{color:#e8503e}.header__link.active::after{content:"";position:absolute;bottom:-4px;left:0;height:2px;width:100%;background-color:#e8503e}.header__link:not(:last-child){margin-right:15px}

body{font-family:"Roboto", Arial, Helvetica, sans-serif}a{text-decoration:none}.container{width:90%;max-width:1300px;margin:0 auto}.info-message{margin:20px 0;padding:15px;text-align:center;color:#2e3442;border:none}.page-title{text-align:center;font-size:40px;color:#2e3442;margin-bottom:20px}

